import { autobind } from 'core-decorators';
import { observable, computed, action } from 'mobx';

export class Photo {
    src: string;
    thumbnail: string;
    width: number;
    height: number;
    lat: number;
    lng: number;
    pano: string;
    zoom: number;
    heading: number;
    pitch: number;
}

export class PhotoStore {
    @observable photos: Array<Photo> = [];
    @observable currentIndex: number = -1;
    @observable cols: number = 1;

    @computed
    get currentPhoto() {
        if (this.photos.length > 0) {
            return this.photos[this.currentIndex];
        }
        return undefined;
    }

    @computed
    get isLightboxOpen() {
        return this.currentPhoto ? true : false;
    }

    @computed
    get images() {
        return this.photos.map(x => {
            return {
                src: x.src,
                width: x.width,
                height: x.height,
            };
        });
    }

    @computed
    get thumbnail() {
        return this.photos.map(x => {
            return {
                src: x.thumbnail,
                width: x.width,
                height: x.height,
            };
        });
    }

    @autobind @action setCols(cols: number) {
        if (cols < 1) {
            cols = 1;
        }
        if (cols > 3) {
            cols = 3;
        }
        this.cols = cols;
    }

    @autobind @action setCurrentImage(index: number) {
        this.currentIndex = index;
    }

    @autobind @action
    next() {
        if (this.currentIndex < this.photos.length - 1) {
            this.currentIndex += 1;
        }
    }
    @autobind @action
    prev() {
        if (this.currentIndex > 0) {
            this.currentIndex -= 1;
        }
    }

    @autobind @action
    load() {
        fetch(`${process.env.PUBLIC_URL}/data.json`, { credentials: 'include' }).then(resp => {
            return resp.json();
        }).then(json => {
            this.photos = json;
        });
    }

    @computed
    get photoJSON() {
        if (!this.photos) {
            return '';
        }
        return JSON.stringify(this.photos, null, '\t');
    }
}


