import { autobind } from 'core-decorators';
import * as React from 'react';
import { Photo } from '../store';

import * as Measure from 'react-measure';

interface StreetViewProps {
    photo?: Photo;
}

export class StreetView extends React.Component<StreetViewProps, {}> {
    private sv: HTMLElement;
    private pano: google.maps.StreetViewPanorama;
    componentDidMount() {
        this.pano = new google.maps.StreetViewPanorama(this.sv);
        google.maps.event.addListener(this.pano, 'status_changed', this.handleStatus);
        google.maps.event.addListener(this.pano, 'pov_changed', this.handleChange);
        google.maps.event.addListener(this.pano, 'zoom_changed', this.handleChange);
        this.componentDidUpdate();
    }
    componentDidUpdate() {
        this.setPhoto();
    }
    setPhoto(uselat: boolean = false) {
        if (!this.props.photo) {
            return;
        }
        const photo = this.props.photo!;

        if (!uselat) {
            this.pano.setPano(photo.pano);
        } else {
            this.pano.setPosition({ lat: photo.lat, lng: photo.lng });
        }

        const { heading, pitch } = photo;

        if (heading && pitch) {
            this.pano.setPov({ heading, pitch });
        } else {
            this.pano.setPov({ heading: 0, pitch: 0 });
        }
        this.setZoom();
    }
    setZoom() {
        if (this.props.photo) {
            this.pano.setZoom(this.props.photo.zoom || 0.01);
        }
    }
    render() {
        return (
            <Measure onMeasure={this.triggerResize}>
                <div
                    onClick={this.disableEvent}
                    onTouchEnd={this.disableEvent}
                    style={{ width: '120vh', maxWidth: '100vw' }} ref={x => this.sv = x} />
            </Measure>
        );
    }
    @autobind
    triggerResize() {
        if (this.pano) {
            google.maps.event.trigger(this.pano, 'resize');
            this.setZoom();
        }
    }
    @autobind
    handleStatus() {
        if (this.pano.getStatus() !== google.maps.StreetViewStatus.OK) {
            this.setPhoto(true);
        }
    }
    @autobind
    disableEvent(e: React.SyntheticEvent<HTMLDivElement>) {
        e.preventDefault();
        e.stopPropagation();
    }
    @autobind
    handleChange() {
        const pov = this.pano.getPov();
        this.props.photo!.heading = pov.heading!;
        this.props.photo!.pitch = pov.pitch!;

        const zoom = this.pano.getZoom();
        this.props.photo!.zoom = zoom!;
    }
}



