import { autobind } from 'core-decorators';

import * as React from 'react';
import './App.css';

import { PhotoStore } from './store';
import { observer } from 'mobx-react';

import * as Measure from 'react-measure';
import * as download from 'downloadjs';
import { StreetView } from './components/StreetView';
const Lightbox = require('react-images');
const Gallery = require('react-photo-gallery');

@observer
class App extends React.Component<{}, {}> {
  private photoStore = new PhotoStore();
  componentDidMount() {
    this.photoStore.load();
  }
  render() {
    const theme = {
      header: {
        flex: 1,
        order: 1
      },
    };
    return (
      <div className="App flex center one">
        <p className="Title">
          <h1>Creatism</h1>
          <a href="https://arxiv.org/abs/1707.03491">[Paper]</a>
        </p>

        {false && process.env.NODE_ENV === 'development' &&
          <a onClick={() => download(this.photoStore.photoJSON, 'data.json', 'application/json')}>Download</a>}
        <Measure onMeasure={this.onMeasure}>
          <div style={{ maxWidth: '100vw', width: '800px' }}>
            <Gallery
              photos={this.photoStore.thumbnail}
              cols={this.photoStore.cols}
              onClickPhoto={this.openLightbox}
              margin={5} />;
        </div>
        </Measure>

        <Lightbox
          customControls={[<StreetView key="streetview" photo={this.photoStore.currentPhoto} />]}
          theme={theme}
          showCloseButton={false}
          images={this.photoStore.images}
          backdropClosesModal={true}
          onClose={this.closeLightBox}
          onClickPrev={this.photoStore.prev}
          onClickNext={this.photoStore.next}
          currentImage={this.photoStore.currentIndex}
          isOpen={this.photoStore.isLightboxOpen}
        />
      </div>
    );
  }
  @autobind
  openLightbox(index: number, event: Event) {
    event.preventDefault();
    this.photoStore.setCurrentImage(index);
  }
  @autobind
  closeLightBox() {
    this.photoStore.setCurrentImage(-1);
  }
  @autobind
  onMeasure(dimensions: Measure.Dimensions) {
    this.photoStore.setCols(Math.min(Math.floor(dimensions.width! / 300), 2));
  }
}

export default App;


